<template>
    <div class="sPay_wrap">
      <div class="sPay">
        <div class="jifennum">
          <div class="jifen">{{count }}</div>
          <span>积分</span>
        </div>
        <div class="duihua">
          <span>兑换密码</span>
          <input type="password" v-model="psdcode" placeholder="请输入你的兑换密码">
        </div>
      </div>
      <van-button type="default" class="duihuan" @click="Change">确认兑换</van-button>
    </div>
</template>

<script>
  import { Toast,Dialog } from 'vant';
import { apiPost } from '../common/js/api';
    export default {
      name: "Header",
      data(){
        const query = this.$route.query;
          return{
            query:query,
            count:query.goods_fee||query.total_integral,
            psdcode:''
          }
      },
      mounted(){
        console.log(this.query)
        console.log(this.count)
      },
      methods:{
        Change(){
          let reg=/^\d{6}$/;
          if(!reg.test(this.psdcode)){
            Toast('密码格式错误');
            return;
          }
          let formData = new FormData();
          formData.append("accessToken",this.$cookie.get('accessToken'))
          formData.append("order_id",this.query.orderId||this.query.order_id)
          formData.append("pwd",this.psdcode)
          apiPost("/goods/pay-order",formData).then(res=>{
            Dialog.alert({
              title: '支付成功',
              message: res.data.message
            }).then(() => {
              this.$router.push({
                path:"/payResult",
                query:{
                  order_id:this.query.orderId||this.query.order_id,
                  result:0
                }
              })
            })
          }).catch(err=>{
            console.log("err",err)
            Dialog.alert({
              title: '支付失败',
              message: err.data.message
            }).then(() => {
              this.$router.push({
                path:"/payResult",
                query:{
                  order_id:this.query.order_id,
                  result:1
                }
              })
            })
          })
        }
      }
    }
</script>

<style scoped lang="scss">
  .sPay_wrap{
    height: 100%;
    background: #E6E9ED;
    padding-top: 0.18rem;
  }
  .sPay{
      width: 3.5rem;
    margin: 0 auto;
    background: white;
    height: 1.88rem;
    overflow: hidden;
    border-radius: 0.04rem;
    .jifennum{
      text-align: center;
      font-size: 0.17rem;
      .jifen{
        color: #3C7FEE;
        margin-top: 0.41rem;
        font-size: 0.42rem;
      }
    }
    .duihua{
      display: flex;
      justify-content: space-between;
      margin-top: 0.1rem;
      padding: 0.13rem;
      span{
        font-size: 0.17rem;
        color: #333;
      }
      input{
        border: none;
        font-size: 0.17rem;
      }
    }

  }
  .duihuan{
    width: 3.06rem;
    height: 0.47rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0.04rem;
    color: white;
    font-size: 0.19rem;
    display: block;
    margin: 1.52rem auto;
    line-height: 0.47rem;
    text-align: center;
  }
</style>



